<i18n>
{
  "en": {
    "TransactionDetails": "Transaction details",
    "Recharge": "Recharge",
    "Consumption": "Consumption",
    "Withdrawal": "Withdrawal"
  },
  "zh": {
    "TransactionDetails": "交易明细",
    "Recharge": "余额充值",
    "Consumption": "余额消费",
    "Withdrawal": "余额提现"
  }
}
</i18n>

<template>
  <div class="transaction">
    <head-top :title='$t("TransactionDetails")' :go-back="true"></head-top>
    <div class="container">
      <div class="tabs">
        <div :class="['tab',{'active': currentTab === tab.type}]" v-for="tab in tabs" :key="tab.type" @click="toggleTab(tab.type)">
          <span>{{tab.title}}</span>
        </div>
      </div>
      <recharge :is='currentTab' keep-alive class="content" ></recharge>
    </div>
  </div>
</template>

<script>
import headTop from '../../components/Header/index'
import consume from './transaction/consume'
import deposit from './transaction/deposit'
import recharge from './transaction/recharge'
import withdraw from './transaction/withdraw'
export default {
  name: 'transaction',
  data () {
    return {
      tabs: [
        {
          title: this.$t('Recharge'),
          type: 'recharge'
        },
        {
          title: this.$t('Consumption'),
          type: 'consume'
        },
        {
          title: this.$t('Withdrawal'),
          type: 'withdraw'
        }
        // ,{
        //   title: '押金明细',
        //   type: 'deposit'
        // }
      ],
      currentTab: 'recharge'
    }
  },

  components: {
    headTop,
    consume,
    deposit,
    recharge,
    withdraw
  },
  methods: {
    toggleTab (type) {
      this.currentTab = type
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.transaction{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    .tabs{
      display: flex;
      .tab{
        height: 80px;
        flex: 1;
        font-size: 28px;
        &.active{
          color: #f4da21;
          span::after{
            content: '';
            display: block;
            .wh(50px,6px);
            background: url('../../assets/wallet_images/sliding.png');
            margin: -10px auto 0 auto;
          }
        }
        span{
          display: block;
          line-height: 80px;
        }
      }
    }
    .content{
      .wh(100%, calc(~"100% - 80px"));
      background: #eeeeee;
      overflow-y: scroll;
    }
  }
}
</style>



