<i18n>
{
  "en": {
    "Empty": "Empty",
    "PaymentSuccess": "Payment success",
    "PaymentFailure": "Payment failure",
    "unit": "￥",
    "Balance": "Balance",
    "WeChat": "WeChat",
    "Alipay": "Alipay",
    "Charger": "Charger",
    "Bettery": "Bettery"
  },
  "zh": {
    "Empty": "空空如也",
    "PaymentSuccess": "付款成功",
    "PaymentFailure": "付款失败",
    "unit": "元",
    "Balance": "余额消费",
    "WeChat": "微信支付",
    "Alipay": "支付宝支付",
    "Charger": "座充",
    "Bettery": "充电宝"
  }
}
</i18n>

<template>
  <div class="consume">
    <div class="empty" v-if="!list.length">
      <img src="../../../assets/wallet_images/nothing.png" alt="">
      <span>{{ $t('Empty') }}</span>
    </div>
    <div class="list" v-if="list.length">
      <div class="item" v-for="item in list" :key="item.id">
        <div class="left">
          <p>
            <span class="text">{{item.text}}</span>
            <span class="state">{{item.state?$t('PaymentSuccess') : $t('PaymentFailure')}}</span>
          </p>
          <span class="time">{{item.time}}</span>
        </div>
        <div class="right">
          <span class="count"><strong>- {{item.count}}</strong>{{ $t('unit') }}</span>
          <span class="type" v-show="item.type==='wechat'">{{ $t('WeChat') }}</span>
          <span class="type" v-show="item.type==='alipay'">{{ $t('Alipay') }}</span>
          <span class="type" v-show="item.type==='balance'">{{ $t('Balance') }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'consume',
  data () {
    return {
      list: [
        {
          id: 1,
          text: this.$t('Charger'),
          state: 1,
          time: '2017.05.09  20:34',
          count: 6,
          type: 'wechat'
        },
        {
          id: 2,
          text: this.$t('Charger'),
          state: 1,
          time: '2017.05.09  20:34',
          count: 6,
          type: 'alipay'
        },
        {
          id: 3,
          text: this.$t('Bettery'),
          state: 0,
          time: '2017.05.09  20:34',
          count: 6,
          type: 'balance'
        }
      ]
    }
  }
}
</script>

<style lang="less" scoped>
.consume{
  .empty{
    img{
      height: 190px;
      margin: 260px auto 0 auto;
    }
    span{
      display: block;
      font-size: 30px;
    }
    a{
      font-style: italic;
      font-size: 30px;
      color: #50a4ff;
      text-decoration: underline;
      display: block;
      margin-top: 90px;
      &:active{
        background: none;
      }
    }
  }
  .list{
    padding-top: 30px;
    .item{
      &:nth-child(1){
        margin: 0 auto;
      }
      &:last-child{
        margin-bottom: 20px;
      }
      margin: 44px auto 0 auto;
      .wh(680px, 150px);
      background: #ffffff;
      border-radius: 20px;
      box-shadow: 0px 4px 25px rgba(130,130,130,0.2); // 左上下右
      display: flex;
      .left{
        .wh(490px, 100%);
        p{
          font-size: 28px;
          height: 75px;
          color: #595959;
          line-height: 75px;
          text-align: left;
          .text{
            margin: 0 50px 0 30px;
          }
        }
        .time{
          display: block;
          .wh-l(100%, 75px);
          text-align: left;
          font-size: 22px;
          color: #999999;
          padding-left: 30px;
        }
      }
      .right{
        width: 190px;
        padding: 30px 30px 0 0;
        position: relative;
        span{
          display: block;
          font-size: 20px;
          color: #999999;
          text-align: right;
          &.count{
            font-size: 30px;
            color: #F4233D;
            strong{
              font-size: 46px;
              margin-right: 10px;
              font-weight: bold;
            }
          }
        }
        .type{
          .wh(140px, 30px);
          .zoom-font(0.7);
          float: right;
          margin-right: -42px;
        }
      }
    }
  }
}
</style>



